.sm-links {
    position: fixed; 
    justify-content: space-evenly;
    top: 79vh;
    left: 1rem;
    display: flex;
    flex-direction: column;
    width: 3.5rem;
    height: 8.5rem;
    background-color: $black;
    div {
        background-repeat: no-repeat;
        background-size: contain;
        height: 1rem;
        width: 100%;
        background-position: center;
        cursor: pointer;
        &.twitter-link  {
            background-image: url('../../Design\ Assets/icon-twitter.svg');
        }
        &.insta-link {
            background-image: url('../../Design\ Assets/icon-instagram.svg');
        }
        &.facebook-link {
            background-image: url('../../Design\ Assets/icon-facebook.svg');
        }
        &.youtube-link {
            background-image: url('../../Design\ Assets/icon-youtube.svg');
        }
    }
    i {
        position: absolute;
        right: -2px;
        top: -2px;
        width: 0;
        height: 0;
        border-color: $yellow transparent  transparent transparent ;
        border-style: solid;
        border-width: 16px 0 0 16px;
    }
}